.shop {
    text-align: center;
}
.shopItem.owned {
    background-color: white;
    border: 1px dashed lightgray;
}
.shopItem.owned .price:hover {
    color: inherit;
}
.shopItem.owned button.price {
    border: 1px solid white;
}

.shopItem {
    background-color: white;
    -ms-flex-line-pack: center;
        align-content: center;
    width: 800px;
    -webkit-box-shadow: darkgray;
            box-shadow: darkgray;
    height: 3em;
    border: 1px solid lightgray;
    padding: 4px;
    margin: 1em;
    display: grid;
    grid-template-columns: 12em 1px auto 80px;
    -webkit-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s; 
}
.name {
    text-align: left;
    grid-column-start: 1;
    font-weight: bold;
}
.price {
    grid-column-start: 4;
    font-size: 14pt;
}
.description {
    grid-column-start: 3;
    text-align: left;
}


